import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import useWindowScrollPosition from "@rehooks/window-scroll-position";
import { Link } from "react-scroll";

const useStyles = makeStyles({
  nav: {
    zIndex: 3,
    position: "fixed",
    width: "100vw",
    height: "90px",
    borderBottom: "1px solid white",
    color: "white",
    transition: "0.5s",
    display:"none",
    "@media(min-width:768px)":{
      display:"block"
    }
  },
  navScrol: {
    zIndex: 3,
    position: "fixed",
    width: "100vw",
    height: "90px",
    borderBottom: "1px solid white",
    color: "black",
    backgroundColor: "#dadbd8",
    transition: "0.5s",
    display:"none",
    "@media(min-width:768px)":{
      display:"block"
    }
  },

  brand: {
    display: "flex",
    textDecoration: "none",
    fontSize: "14px",
    fontWeight: 300,
    letterSpacing: "4px",
    alignItems: "center",
    justifyContent: "center",
  },
  menu: {
    position: "relative",
    display: "flex",
    justifyContent: "space-around",
    alignItems: "center",
    width: "100vw",
    listStyle: "none",
    padding:0
  },
  li: {
    height: "60px",
    width: "110px",
    cursor: "pointer",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    "&:hover": {
      backgroundColor: "white",
      color: "black",
    },
  },
});

const Nav = () => {
  const classes = useStyles();

  let position = useWindowScrollPosition();

  return (
    <div className={position.y < 100 ? classes.nav : classes.navScrol}>
      <div className={classes.brand}>
        <ul className={classes.menu}>
          <Link to="Home" smooth={true} duration={800}>
            <li className={classes.li}>Home</li>
          </Link>
          <Link to="Rooms" smooth={true} duration={800} offset={-90}>
            <li className={classes.li}>Rooms</li>
          </Link>
          <Link to="Info" smooth={true} duration={800} offset={-90}>
            <li className={classes.li}>Info</li>
          </Link>
          <Link to="Attractions" smooth={true} duration={800} offset={-90}>
            <li className={classes.li}>Attractions</li>
          </Link>
          <Link to="Restaurant" smooth={true} duration={800} offset={-90}>
            <li className={classes.li}>Restaurant</li>
          </Link>
          <Link to="Contact" smooth={true} duration={800} offset={-90}>
            <li className={classes.li}>Contact</li>
          </Link>
        </ul>
      </div>
    </div>
  );
};
export default Nav;
