import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import easy from "./images/easy.jpg";
import medium from "./images/medium.jpg";
import hard from "./images/hard.jpg";
import RoomItem from "./RoomItem";

const useStyles = makeStyles({
  container: {
    position: "relative",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "space-around",
    padding:20,
    "@media(min-width:768px)": {
      flexDirection: "row",
    },
  },
  title: {
    color: "black",
    fontSize: "30px",
    fontWeight: 300,
  },
});

const RoomList = [
  {
    title: "DELUX",
    img: easy,
    price: 100,
    // stars:<i class="fas fa-star"></i>
  },
  {
    title: "SUITE",
    img: medium,
    price: 150,
  },
  {
    title: "SUPERIOR",
    img: hard,
    price: 200,
  },
];

const Rooms = () => {
  const classes = useStyles();
  return (
      <div>
          <h1 className={classes.title}>Popular offerts</h1>
    <div className={classes.container} id="Rooms">
      
      {RoomList.map((item) => {
        return (
          <RoomItem
            key={item.title}
            title={item.title}
            img={item.img}
            price={item.price}
            // stars={item.stars}
          />
        );
      })}
    </div>
    </div>
  );
};

export default Rooms;
