import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import spa from "./images/spa.jpg";
import diving from "./images/diving.jpg";
import island from "./images/island.jpg";
import AttractionsItem from "./AttractionsItem";

const useStyles = makeStyles({
  container: {
    position: "relative",
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-around",
    alignItems: "center",
    padding:20,
    "@media(min-width:1024px)": {
      flexDirection: "row",
    },
  },
  title: {
    color: "black",
    fontSize: "30px",
    fontWeight: 300,
  },
});

const AttractionsList = [
  {
    title: "SPA",
    img: spa,
    paragraph:"Comfortable space in which each of you will feel special and take a break from everyday life. "
  },
  {
    title: "DIVING",
    img: diving,
    paragraph:"The best diving sites, provides unforgettable impressions and views."
  },
  {
    title: "PRIVATE ISLAND",
    img: island,
    paragraph:"We give you the opportunity to rent a private island where you can spend the whole day."
  },
];

const Rooms = () => {
  const classes = useStyles();
  return (
    <div id="Attractions">
      <h1 className={classes.title}>Attractions</h1>
      <div className={classes.container}>
        {AttractionsList.map((item) => {
          return (
            <AttractionsItem
              key={item.title}
              title={item.title}
              img={item.img}
              paragraph={item.paragraph}
            />
          );
        })}
      </div>
    </div>
  );
};

export default Rooms;
