import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Link } from "react-scroll";

const useStyles = makeStyles({
  nav: {
    zIndex: 3,
    position: "fixed",
    width: "65vw",
    height: "100vh",
    borderBottom: "1px solid white",
    color: "white",
    transition: "1s",
    backgroundColor: "#dadbd8",
    left: "-100%",
    "@media(min-width:768px)":{
      display:"none"
    }
  },
  activeNav: {
    zIndex: 3,
    position: "fixed",
    width: "65vw",
    height: "100vh",
    borderBottom: "1px solid white",
    color: "white",
    transition: "1s",
    backgroundColor: "#dadbd8",
    left:0,
    "@media(min-width:768px)":{
      display:"none"
    }
  },

  brand: {
    display: "flex",
    position: "absolute",
    textDecoration: "none",
    fontSize: "14px",
    fontWeight: 300,
    letterSpacing: "4px",
    alignItems: "center",
    justifyContent: "center",
    width: "100%",
    height: "100%",
  },
  menu: {
    position: "relative",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    // width: "100%",
    // height:"100%",
    listStyle: "none",
    padding: 0,
  },
  li: {
    height: "50px",
    width: "110px",
    cursor: "pointer",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    color:"black",
    "&:hover": {
      backgroundColor: "white",
      // color: "black",
    },
  },
    close:{
position: "relative",
float:"right",
zIndex:10
    }
});

const Nav = () => {
  const classes = useStyles();
  const [visible, setVisible] = useState(false)

  const close = () => {
    setVisible(false)
  }
  const open = () => {
    setVisible(true)
  }

  return (
    <div>
      <i class="fas fa-bars" id="bar" onClick={open}></i>
    
    <div className={visible == false  ? classes.nav : classes.activeNav} >
      <div onClick={close} className={classes.close}>
      <i class="fas fa-times" id="close" ></i>
      </div>
      <div className={classes.brand}>
        <ul className={classes.menu}>
          <Link to="Home" smooth={true} duration={800} onClick={close} >
            <li className={classes.li}>Home</li>
          </Link>
          <Link to="Rooms" smooth={true} duration={800} onClick={close}>
            <li className={classes.li}>Rooms</li>
          </Link>
          <Link to="Info" smooth={true} duration={800} onClick={close}>
            <li className={classes.li}>Info</li>
          </Link>
          <Link to="Attractions" smooth={true} duration={800} onClick={close}>
            <li className={classes.li}>Attractions</li>
          </Link>
          <Link to="Restaurant" smooth={true} duration={800} onClick={close}>
            <li className={classes.li}>Restaurant</li>
          </Link>
          <Link to="Contact" smooth={true} duration={800} onClick={close}>
            <li className={classes.li}>Contact</li>
          </Link>
        </ul>
      </div>
    </div>
    </div>
  );
};
export default Nav;
